.go-top{position:fixed;cursor:pointer;top:0;right:15px;color:#ffffff;background-color:#076a76;z-index:4;width:40px;text-align:center;height:40px;line-height:40px;opacity:0;visibility:hidden;-webkit-transition:.9s;transition:.9s;border-radius:50%;}

