.go-top i{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);left:0;right:0;margin:0 auto;-webkit-transition:0.5s;transition:0.5s;}

.go-top i:last-child{opacity:0;visibility:hidden;top:60%;}

.go-top::before{content:'';position:absolute;top:0;left:0;width:100%;height:100%;z-index:-1;background:#076a76;opacity:0;visibility:hidden;-webkit-transition:0.5s;transition:0.5s;border-radius:50%;}

.go-top:hover, .go-top:focus{color:#ffffff;}

.go-top:hover::before, .go-top:focus::before{opacity:1;visibility:visible;}

.go-top:hover i:first-child, .go-top:focus i:first-child{opacity:0;top:0;visibility:hidden;}

.go-top:hover i:last-child, .go-top:focus i:last-child{opacity:1;visibility:visible;top:50%;}

