  .footer-section .social-btns .btn.facebook .fa {color: #3b5998;}
  
  .footer-section .social-btns .btn.twitter:before {background: #3cf;}
  .footer-section .social-btns .btn.twitter .fa {color: #3cf;}
  
  .footer-section .social-btns .btn.google:before {background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
