.icon {
    transition: color 0.25s;
    text-align: center;
}


.icon::before,
.icon::after {
  border: 2px solid transparent;
  width: 0;
  height: 0;
}


.icon::before {
    top: 0;
    left: 0;
  }

  .icon::after {
    bottom: 0;
    right: 0;
  }

  .icon:hover {
    color: #fff;
  }


  .icon:hover::before,
  .icon:hover::after {
    width: 100%;
    height: 100%;
  }

.icon:hover::before {
    border-top-color: #000; 
    border-right-color: #000;
    transition: 0.25s ease-out, 
    0.25s ease-out 0.25s;
  }

.icon:hover::after {
    border-bottom-color: #000;
    border-left-color: #000;
    transition:
     0s ease-out 0.5s, 
      0.25s ease-out 0.5s,
       0.25s ease-out 0.75s;
  }

