    content: " ";
    text-align: center;
    display: block;
    line-height: 1em;
    width: 1em;
    height: 1em;
    position: absolute;
    top: calc(50% - 1.875em/2);
    margin-top: -.5em;
    background: #076a76;
    border-radius: 50%;
    z-index: 1;
    left: auto;
    right: 52px
  }

