/* /////////////////////////////// New contact page design ////////////// */

.contact_head {
  position: relative;
  display: block;
  width: 100%;
  background: linear-gradient(#0e0e0e4d, #1c1c0f8c),url(/cwp_website/static/media/contactbanner.4bab38de.jpg) center no-repeat;
  background-size: cover;
  padding: 60px 0 30px;
  height: 40vh;
  background-attachment: fixed;
}

.contact_head .section-title h1, .contact_head .section-title p {color: #fff;}

.Contact_Map {
  padding: 0 !important;
}

.Contact_Map .responsive-map {
  height: 450px;
  padding: 0;
}

.contact-area {
  position: relative;
  width: 100%;
  background: linear-gradient(#0e0e0e4d, #1c1c0f8c),url(/cwp_website/static/media/contactbanner.4bab38de.jpg) center no-repeat;
  background-size: cover;
  padding: 0px 0 30px;
  background-attachment: fixed;
}

.Contact_Form {
  max-width: 950px;
  display: block;
  box-shadow: 0px 0px 14px 2px #0000003d;
  background: #fff;
  padding: 30px;
  margin: auto;
  top: -55px;
  position: relative;
}

.contact_left {
  display: block;
  position: relative;
  background: linear-gradient(#1ab6c8, #116c80);
  padding: 15px 20px;
  box-shadow: 0px 0px 6px 0px #0000008c;
  right: 60px;
}
.contact_left h3{
  color: #fff;
}
.contact_left ul {list-style-type: none; padding: 0;}

.contact_left ul li .icon {
  padding: 6px 10px;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0px 0px 2px 2px #d7d7d791;
  margin-right: 15px;
  display: block;
  width: 35px;
  height: 35px;
  line-height: 0;
  transition: 0.3s;
}

.contact_left ul li {
  display: -webkit-flex;
  display: flex;
  margin: 25px 0;
}

.contact_left ul li:hover .icon {
  -webkit-transform: translate(0, -5px);
          transform: translate(0, -5px);
}

.contact_left ul li .icon i {
  font-size: 20px !important;
  font-weight: 600; 
  background: linear-gradient(to bottom, #116c80 0%,#1ab6c8 100%);
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color:transparent;
}

.contact_left ul li .content {
  color: #fff;
  font-weight: 600;
  font-size: 14px;
}

.contact_left ul li .content a {color: #fff;}

#contactForm {
  position: relative;
  right: 30px;
  
}

#contactForm h3 {
  color: #000;
  margin-bottom: 10px;
}
